import { Button, Grid, Link, Typography, Toolbar, AppBar, Slide, useScrollTrigger, useMediaQuery } from "@mui/material";
import { makeStyles } from '@mui/styles';
import theme from "../theme";
import { useState } from "react";
import { useLayoutEffect } from "react";
import { useSelector } from "react-redux";
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import AutoAwesomeIcon from '@mui/icons-material/AutoAwesome';

const useStyles = makeStyles((_) => ({
    root: {
        height: "50px"
    },
    titleText: {
        flexGrow: 1,
        [theme.breakpoints.down('lg')]: {
            fontSize: "24px !important",
            lineHeight: "1.3 !important",
            paddingTop: "10px",
            paddingBottom: "10px",
        }
    },
    navigationContainer: {
        paddingRight: "25px",
        "& *:hover": {
            cursor: "pointer",

        },
        "& *": {
            marginLeft: "35px !important"
        },
        [theme.breakpoints.down('lg')]: {
            paddingRight: "0px",
            "& *": {
                marginLeft: "0px !important"
            },
        }
    },
    imageDisplayer: {
        width: "100%",
        height: "100%",
        top: 0,
        left: 0,
        position: "fixed",
        zIndex: "1",
        display: "flex",
        "& img": {
            width: "80vw",
            height: "80vh",
            zIndex: "2 !important",
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%,-50%)",
            objectFit: "contain"
        },
        "& div": {
            backgroundColor: theme.palette.primary.main,
            opacity: "0.8",
            position: "absolute",
            width: "100%",
            height: "100%",
            top: 0,
            left: 0,
            zIndex: "1 !important"
        }
    },
}));

function AboveLgNavigation(props) {
    const { data } = props;
    const scrollToThePage = (pageId) => {
        var element = document.getElementById(pageId);
        const matrix = new DOMMatrixReadOnly(window.getComputedStyle(element).transform);
        const headerOffset = matrix.m42;
        const elementPosition = element.getBoundingClientRect().top;
        const offsetPosition = elementPosition + window.pageYOffset - headerOffset;
        window.scrollTo({
            top: offsetPosition,
            behavior: "smooth"
        });
    }
    return (
        <div>
            {
                data.map((item, index) => {
                    return (
                        <Link key={index} onClick={() => scrollToThePage(item)} underline="hover" color={"inherit"}>
                            {item}
                        </Link>
                    )
                })
            }
        </div>)
}

function BelowLgNavigation(props) {
    const { data } = props;
    const [anchorElNav, setAnchorElNav] = useState(null);
    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };
    const scrollToThePage = (pageId) => {
        var element = document.getElementById(pageId);
        const matrix = new DOMMatrixReadOnly(window.getComputedStyle(element).transform);
        const headerOffset = matrix.m42;
        const elementPosition = element.getBoundingClientRect().top;
        const offsetPosition = elementPosition + window.pageYOffset - headerOffset;
        window.scrollTo({
            top: offsetPosition,
            behavior: "smooth"
        });
        handleCloseNavMenu();
    }
    return (
        <div>
            <IconButton
                size="large"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
            >
                <MenuIcon />
            </IconButton>
            <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
            >
                {data.map((item, index) => (
                    <MenuItem key={index} onClick={() => scrollToThePage(item)}>
                        <Typography color={theme.palette.secondary.main} textAlign="center">{item}</Typography>
                    </MenuItem>
                ))}
            </Menu>
        </div>
    )
}

function Header() {
    const classes = useStyles();
    const trigger = useScrollTrigger();
    const [isShowingImage, setIsShowingImage] = useState(false);
    const [shownImage, setShownImage] = useState("");
    const data = useSelector(state => state.header.data);
    const isBelowLg = useMediaQuery(theme.breakpoints.down('lg'));

    useLayoutEffect(() => {
        document.querySelectorAll("img").forEach(element => {
            if (!element.classList.contains("excludeExpansion")) {
                element.style.cursor = "pointer";
                element.onclick = () => {
                    setShownImage(element.src);
                    setIsShowingImage(true);
                };
            }
        });
    }, []);
    return (
        <Grid container direction={"column"} className={classes.root} >
            <Slide appear={false} direction="down" in={!trigger && !isShowingImage}>
                <AppBar position="fixed" color="secondary">
                    <Toolbar>
                        <Button
                            size="large"
                            edge="start"
                            color="inherit"
                            aria-label="menu"
                            sx={{ mr: 2 }}
                        >
                            <AutoAwesomeIcon fontSize="large" />
                        </Button>
                        <Typography variant="h6" component="div" className={classes.titleText}>
                            Rezpage
                        </Typography>
                        <div className={classes.navigationContainer}>
                            {
                                isBelowLg ?
                                    <BelowLgNavigation data={data} />
                                    : <AboveLgNavigation data={data} />
                            }
                        </div>
                    </Toolbar>
                </AppBar>
            </Slide>
            <Grid container className={classes.imageDisplayer} display={isShowingImage ? "block" : "none"} onClick={() => setIsShowingImage(false)}>
                <div />
                <img src={shownImage} />
            </Grid>
        </Grid >
    );
}

export default Header;
