import { Grid, List, ListItem, ListItemText, Tooltip, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { Carousel } from "../utils/carousels";
import { useSelector } from "react-redux";
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import theme from "../theme";
import useMediaQuery from '@mui/material/useMediaQuery';
import { SvgButton } from "../utils/buttons";
import OndemandVideoIcon from '@mui/icons-material/OndemandVideo';
import { Box } from "@mui/system";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "75px 6vw 50px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
    titleSubText: {
        paddingBottom: "25px",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
    card: {
        border: "2px solid #62656c",
        borderRadius: "25px",
        height: "575px",
        padding: "10px",
        flexShrink: "0",
        display: "inline-block",
        whiteSpace: "normal",
        verticalAlign: "top",
        textAlign: "center",
        overflow: "scroll"
    },
    cardImageCarouselRoot: {
        position: "relative",
        marginBottom: "25px",
        width: "200px",
        maxWidth: "200px",
        marginLeft: "auto",
        marginRight: "auto",
        [theme.breakpoints.down('md')]: {
            width: "75%",
        }
    },
    cardImageCarousel: {
        width: "200px",
        height: "200px",
        marginRight: "auto",
        marginLeft: "auto",
        marginTop: "10px",
        [theme.breakpoints.down('md')]: {
            width: "100%",
        }
    },
    cardImageCard: {
        flexShrink: "0",
        display: "inline-block",
        whiteSpace: "normal",
        verticalAlign: "top",
        "& img": {
            width: "100%",
            height: "100%",
            objectFit: "contain"
        },
        [theme.breakpoints.down('md')]: {
            width: "100% !important",
            textAlign: "center"
        }
    },
    cardImageNavigation: {
        height: "0",
        padding: "0",
        "& h6": {
            verticalAlign: "middle",
            marginLeft: "15px",
            marginRight: "15px",
            fontSize: "20px",
            position: "absolute",
            bottom: "-15px",
        },
        "& div": {
            width: "40px",
            height: "40px"
        },
        "& .nextArrow": {
            position: "absolute",
            top: "50%",
            left: "110%",
            transform: "translate(0px,-50%)",
        },
        "& .previousArrow": {
            position: "absolute",
            top: "50%",
            right: "110%",
            transform: "translate(0px,-50%)",
        },
        [theme.breakpoints.down('lg')]: {
            "& .nextArrow": {
                left: "105%",
                transform: "translate(0%,-50%)",
            },
            "& .previousArrow": {
                right: "105%",
                transform: "translate(0%,-50%)",
            },
        }

    },
    cardTitle: {
        lineHeight: "30px !important",
        fontWeight: "bold !important"
    },
    cardList: {
        padding: "10px 0px 12.5px 0px !important",
        "& li": {
            padding: "0",
        },
        "& p": {
            display: "list-item",
            marginLeft: "auto",
            marginRight: "auto",
            listStyleType: "circle",
            listStylePosition: "inside"
        }
    },
    linksContainer: {
        paddingTop: "15px",
        justifyContent: "center",
        "& div": {
            width: "45px",
            height: "45px"
        },
        "& svg": {
            width: "25px",
            height: "25px"
        }
    }
}));


function ProjectCardImage(props) {
    const classes = useStyles();
    const { item, width, height, marginRight } = props;
    return (
        <Grid item className={classes.cardImageCard} sx={{ width: `${width}px`, height: height, marginRight: `${marginRight}px` }} >
            <img src={item} />
        </Grid >)
}

function ProjectCard(props) {
    const classes = useStyles();
    const { item, width, marginRight } = props;
    return (
        <Grid item className={classes.card} sx={{ width: `${width}px`, marginRight: `${marginRight}px` }} >
            <Carousel
                rootClassName={classes.cardImageCarouselRoot}
                carouselClassName={classes.cardImageCarousel}
                navigationClassName={classes.cardImageNavigation}
                CarouselCardElement={ProjectCardImage}
                data={item.imageSources}
                perIndexLength={1}
                NextButtonSvg={ArrowRightIcon}
                PreviousButtonSvg={ArrowLeftIcon}
                widthMultiplier={1}
                heighMultiplier={0.9}
                marginRightMultiplier={0.03}
            />
            <Typography variant="subtitle1" className={classes.cardTitle} >
                {item.name}
            </Typography>
            <List className={classes.cardList}>
                {
                    Object.entries(item.specification).map(([key, value], index) => {
                        return (
                            <ListItem key={index}>
                                <Typography variant="body1" textAlign={"center"}>
                                    {key + " - " + value}
                                </Typography>
                            </ListItem>
                        )
                    })
                }
            </List>
            <Typography variant="body1" marginBottom={"5px"}>
                {item.scopesOfWork}
            </Typography>
            <Typography variant="body1" color={"grey"} >
                {item.category}
            </Typography>
            <Grid container className={classes.linksContainer}>
                {item.links.map((link, index) => {
                    return (
                        <Tooltip title="Watch its showcase video" key={index} >
                            <Box>
                                <SvgButton onClick={() => window.open(link.link, '_blank').focus()}>
                                    <OndemandVideoIcon />
                                </SvgButton>
                            </Box>
                        </Tooltip>
                    )
                })}
            </Grid>
        </Grid >)
}

function Projects() {
    const classes = useStyles();
    const data = useSelector(state => state.projects.data);
    const isBelowMd = useMediaQuery(theme.breakpoints.down('md'));
    const isBelowLg = useMediaQuery(theme.breakpoints.down('lg'));
    return (
        <Grid container className={classes.root + " slideRightAnimation animated"} id="Projects">
            <Grid item xs={12}>
                <Typography variant="h2" className={classes.titleTexts}>
                    Projects
                </Typography>
            </Grid>
            <Grid item>
                <Typography variant="h6" className={classes.titleSubText}>
                    Several projects I made when being a student and employee
                </Typography>
            </Grid>
            <Carousel
                CarouselCardElement={ProjectCard}
                data={data}
                perIndexLength={isBelowMd ? 1 : isBelowLg ? 2 : 3}
                widthMultiplier={isBelowMd ? 1.0 : isBelowLg ? 0.475 : 0.325}
                marginRightMultiplier={isBelowMd ? 0.1 : isBelowLg ? 0.05 : 0.0125} />

        </ Grid >
    );
}

export default Projects;
