import { Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { useSelector } from "react-redux";
import StarIcon from '@mui/icons-material/Star';
import FavoriteIcon from '@mui/icons-material/Favorite';
import StarHalfIcon from '@mui/icons-material/StarHalf';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import theme from "../theme";
import { height } from "@mui/system";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "50px 6vw 50px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px"
    },
    cardDivider: {
        borderBottomWidth: "medium !important",
        maxWidth: "1000px"
    },
    card: {
        flexDirection: "column !important",
        alignItems: "center",
        justifyContent: "center",
        display: "flex",
        borderRadius: "10px",
        height: "75px",
        border: "1px solid gold",
        position: "relative",
        "& h6": {
            lineHeight: "1.4",
            textAlign: "center"
        },
        [theme.breakpoints.down('md')]: {
            maxWidth: "400px",
            marginLeft: "auto",
            marginRight: "auto",
        }
    },
    goldCard: {
        border: "1.75px solid gold",
    },
    silverCard: {
        border: "1.5px solid silver",
    },
    bronzeCard: {
        border: "1px solid #CD7F32",
    },
    starContainer: {
        justifyContent: "center",
        "& #starRoot": {
            position: "relative",
            height: "15px",
            width: "15px",
            marginLeft: "5px",
            marginRIght: "5px",
            "& svg": {
                position: "absolute",

                height: "15px",
                width: "15px",
                color: "black",
                filter: "invert(90%) sepia(28%) saturate(6402%) hue-rotate(1deg) brightness(104%) contrast(106%)"
            }
        }
    },
    favoriteIcon: {
        position: "absolute",
        width: "12.5px",
        height: "12.5px",
        right: "7.5px",
        top: "5px",
        "& svg": {
            color: "#e62958",
            width: "100%",
            height: "100%",
            opacity: "0.75"
        }
    }
}));
function ProficiencyCard(props) {
    const classes = useStyles();
    const { item } = props;
    return (
        <Grid item xs={12} md={6} lg={2} >
            <div className={`${classes.card} ${item.value >= 4 ? classes.goldCard : (item.value >= 3.5 ? classes.silverCard : classes.bronzeCard)}`} >
                {item.loved ?
                    <div className={classes.favoriteIcon}>
                        <FavoriteIcon fontSize="small" />
                    </div> : <div />
                }

                <Typography variant="subtitle1" >
                    {item.name}
                </Typography>
                <Grid container className={classes.starContainer}>
                    {
                        Array.from(Array(5), (e, i) => {
                            const substractedValue = item.value - i;
                            return (
                                <div key={i} id="starRoot">
                                    <StarBorderIcon id="starBorder" />
                                    {substractedValue >= 1 ? <StarIcon id="starFill" /> :
                                        (substractedValue >= 0.5 ? <StarHalfIcon /> : <div />)}
                                </div>)
                        })
                    }
                </Grid >
            </div>
        </Grid >)
}

function Proficiency() {
    const classes = useStyles();
    const data = useSelector(state => state.proficiency.data);
    return (
        <Grid container className={classes.root + " slideRightAnimation animated"} id="Proficiency">
            <Typography variant="h2" className={classes.titleTexts}>
                Proficiency
            </Typography>
            <Grid container variant="row" spacing={2} alignItems="center">
                {
                    data.map((item, index) => {
                        return (
                            <ProficiencyCard
                                key={index}
                                item={item} />
                        )
                    })
                }
            </Grid>
        </ Grid >
    );
}

export default Proficiency;
