import { Divider, Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { VerticalGap } from "../utils/gaps";
import { useSelector } from "react-redux";
import theme from "../theme";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "75px 6vw 50px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
    },
    subtitleTexts: {
    },
    cardDescription: {
        [theme.breakpoints.down('md')]: {
            width: "100%",
            textAlign: "center"
        }
    },
    cardImage: {
        width: "100px",
        height: "100px",
        marginRight: "25px",
        [theme.breakpoints.down('md')]: {
            marginRight: "auto",
            marginLeft: "auto",
            marginBottom: "20px",
            display: "block"
        }
    },
    cardDivider: {
        borderColor: "#62656c !important",
        borderBottomWidth: "medium !important",
        maxWidth: "1000px"
    },

}));

function EducationCard(props) {
    const classes = useStyles();
    const { imageSource, campusName, majorName, attendingDate } = props;
    const data = useSelector(state => state.education.data);
    return (
        <Grid item xs={12} >
            <Grid container direction={"row"}>
                <img className={classes.cardImage} src={imageSource} />
                <Grid item xs={12} md>
                    <Grid container  >
                        <div className={classes.cardDescription}>
                            <Typography variant="h5" >
                                {campusName}
                            </Typography>
                            {/* <VerticalGap distance="5px" /> */}
                            <Typography variant="subtitle1" >
                                {majorName}
                            </Typography>
                            <Typography variant="subtitle1" >
                                {attendingDate}
                            </Typography>
                        </div>
                    </Grid>
                </Grid>
            </Grid>
            <VerticalGap distance={9} />
            <Divider className={classes.cardDivider} />
        </Grid >)
}

function Education() {
    const classes = useStyles();
    const data = useSelector(state => state.education.data);
    return (
        <Grid container rowSpacing={3} className={classes.root + " slideRightAnimation animated"} id="Education">
            <Typography variant="h2" className={classes.titleTexts}>
                Education
            </Typography>
            {
                data.map((item, index) => {
                    return (
                        <EducationCard
                            key={index}
                            imageSource={item.imageSource}
                            campusName={item.campusName}
                            majorName={item.majorName}
                            attendingDate={item.attendingDate} />
                    )
                })
            }
        </ Grid >
    );
}

export default Education;
