import React from "react";
import About from "./components/about";
import Contact from "./components/contact";
import Education from "./components/education";
import Experience from "./components/experience";
import Footer from "./components/footer";
import Header from "./components/header";
import Courses from "./components/courses";
import Projects from "./components/projects";
import Proficiency from "./components/proficiency";
import { useMemo, useLayoutEffect } from "react";
import { makeStyles } from '@mui/styles';
import RecentActivities from "./components/recentActivities";

const useStyles = makeStyles((_) => ({
  slideDownAnimationBefore: {
    opacity: "0",
    transform: "translate(0,-75px)"
  },
  slideDownAnimationAfter: {
    opacity: "1",
    transition: "opacity 0.7s, transform 0.7s",
    transitionTimingFunction: "ease-out",
    transform: "translate(0,0px)"
  },
  slideRightAnimationBefore: {
    opacity: "0",
    transform: "translate(-75px,0px)"
  },
  slideRightAnimationAfter: {
    opacity: "1",
    transition: "opacity 0.7s, transform 0.7s",
    transitionTimingFunction: "ease-out",
    transform: "translate(0,0px)"
  },
}));

function App() {
  const classes = useStyles();
  const slideDownObserver = useMemo(() =>
    new IntersectionObserver((entries) =>
      entries.forEach(x => {
        if (x.isIntersecting) {
          x.target.classList.add(classes.slideDownAnimationAfter);
          x.target.classList.remove(classes.slideDownAnimationBefore);
          slideDownObserver.unobserve(x.target);
        }
      }), { threshold: [0.3, 0.9] })
    , []);
  const slideRightObserver = useMemo(() =>
    new IntersectionObserver((entries) =>
      entries.forEach(x => {
        if (x.isIntersecting) {
          x.target.classList.add(classes.slideRightAnimationAfter);
          x.target.classList.remove(classes.slideRightAnimationBefore);
          slideDownObserver.unobserve(x.target);
        }
      }), { threshold: [0.3, 0.9] })
    , []);

  useLayoutEffect(() => {
    slideDownObserver.disconnect();
    const animatedElements = document.querySelectorAll('.animated');
    if (animatedElements) {
      animatedElements.forEach(el => {
        el.classList.forEach(elClass => {
          if (elClass === "slideDownAnimation") {
            el.classList.add(classes.slideDownAnimationBefore);
            slideDownObserver.observe(el);
          }
          else if (elClass === "slideRightAnimation") {
            el.classList.add(classes.slideRightAnimationBefore);
            slideRightObserver.observe(el);
          }
        })
      })
    }
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  }, []);
  return (
    <React.Fragment >
      <Header />
      <About />
      {/* <Proficiency />
      <Education />
      <Experience /> */}
      <Projects />
      <Courses />
{/*       <RecentActivities /> */}
      <Contact />
      <Footer />
    </React.Fragment>
  );
}

export default App;
